.study-detail {
  &__main {
    margin: 0;
    overflow: hidden;
  }

  &__video {
    padding: 0;

    &-hidden {
      position: absolute;
      top: -100%;
      left: -100%;
    }
  }

  &__side {
    max-height: 804px;
    overflow: auto;
    padding: 0;
    background: #434343;
  }

  &__top {
    padding: 56px 30px;
    border-bottom: 2px solid #4D4D4D;
  }

  &__course {
    font-size: 36px;
    font-weight: 400;
    color: #FFFFFF;
    margin-bottom: 30px;
  }

  &__teacher {
    margin-bottom: 0;
    font-size: 24px;
    color: #999999;
    line-height: 1;
  }

  &__list {
    padding: 8px 0;
    margin-bottom: 0;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 36px 30px;
    color: #999999;
    cursor: pointer;
    text-decoration: unset;

    .iconfont {
      font-size: 40px;
    }

    &-name {
      margin-left: 12px;
      margin-bottom: 0;
    }

    &.active {
      color: #FFFFFF;
    }

    &:hover {
      color: #FFFFFF;
      background: #474747;
      text-decoration: unset;
    }

    & + & {
      margin-top: 8px;

      &:before {
        content: '';
        position: absolute;
        top: -24px;
        left: 50px;
        display: inline-block;
        width:2px;
        height:40px;
        background: #666666;
      }
    }
  }

  &__info {
    padding: 35px 40px;
    background: #FFFFFF;

    &-name {
      margin-bottom: 0;
      font-weight: 400;
      color: #333333;
      line-height: 1;
    }
  }

  &__views {
    display: flex;
    align-items: center;
    margin-top: 20px;
    color: #999999;

    .iconfont {
      margin-right: 12px;
      font-size: 20px;
    }

    &-num {
      font-size: 14px;
    }
  }

  &__intro {
    margin: 2px 0 10px;
    padding: 50px 40px 80px;
    background: #FFFFFF;

    &-avatar {
      width:50px;
      height:50px;
      background:#DCDCDC;
      border-radius:50%;
      object-fit: contain;
    }

    &-name {
      margin-bottom: 0;
      font-weight: bold;
      color: #333333;
      line-height: 1;
    }

    &-date {
      margin-top: 14px;
      margin-bottom: 40px;
      font-size: 14px;
      color: #999999;
      line-height: 1;
    }

    &-content {
      margin-bottom: 0;
      color: #333333;
      line-height: 1;
    }
  }
}

@media (max-width: 1199px) {
  .study-detail {
    &__side {
      max-height: none;
    }
  }
}
